const now = new Date(
  new Date().getFullYear(),
  new Date().getMonth(),
  new Date().getDate()
);

const tomorrow = addDays(now, 1);
const afterWeek = addWeeks(now, 1);

const generateEvents = (start, end) => {
  const calendarRef = useRef(null);
  const getCalendarApi = () => calendarRef.current.getApi();
  const { user } = useFirebase();
  const functions = useFunctions();
  const [apiKey, setApiKey] = useState(undefined);
  const [availiableTime, setAvaliableTime] = useState([]);

  useEffect(() => {
    if (user !== null) {
      console.log("user", user);
      setApiKey(process.env.GATSBY_GOOGLE_CALENDAR_LEWKAN_API_PUBLIC_KEY);
      const getUserEvents = functions.httpsCallable("getUserEvents");
      getUserEvents().then(response => {
        const events = response.data;
        if (events.length < 1) {
          setAvaliableTime(generateEvents(now, afterWeek));
        }
      });
    }
  }, [user]);

  const openDay = e => {
    if (e.date >= now && e.date <= afterWeek) {
      getCalendarApi().changeView("timeGridDay", e.dateStr);
    }
  };

  const openSubscription = e => {
    if (e.allDay) return;
    if (e.date > tomorrow && e.date <= afterWeek) {
      const queryParams = new URLSearchParams({
        date: e.dateStr,
      });

      navigate(`/dashboard/lesson/create/${queryParams.toString()}`);
    }
  };
  const events = [];
  for (let day = start; day <= end; day = addDays(day, 1)) {
    events.push({
      start: day,
      allDay: true,
      display: "background",
    });
    for (let hour = 0; hour < 24; hour++) {
      const start = addHours(day, hour);
      events.push({
        start: start,
        end: addMinutes(start, 30),
        display: "background",
      });
      events.push({
        start: addMinutes(start, 30),
        end: addHours(start, 1),
        display: "background",
      });
    }
  }
  return events;
};

const renderEventContent = eventInfo => {
  return (
    <>
      <b>{eventInfo.timeText}</b>
      <i>{eventInfo.event.title}</i>
    </>
  );
};

const SchedulePage = () => {
  const calendarRef = useRef(null);
  const getCalendarApi = () => calendarRef.current.getApi();
  const { user } = useFirebase();
  const functions = useFunctions();
  const [apiKey, setApiKey] = useState(undefined);
  const [availiableTime, setAvaliableTime] = useState([]);

  useEffect(() => {
    if (user !== null) {
      console.log("user", user);
      setApiKey(process.env.GATSBY_GOOGLE_CALENDAR_LEWKAN_API_PUBLIC_KEY);
      const getUserEvents = functions.httpsCallable("getUserEvents");
      getUserEvents().then(response => {
        const events = response.data;
        if (events.length < 1) {
          setAvaliableTime(generateEvents(now, afterWeek));
        }
      });
    }
  }, [user]);

  const openDay = e => {
    if (e.date >= now && e.date <= afterWeek) {
      getCalendarApi().changeView("timeGridDay", e.dateStr);
    }
  };

  const openSubscription = e => {
    if (e.allDay) return;
    if (e.date > tomorrow && e.date <= afterWeek) {
      navigate(`/dashboard/lesson/create/${e.dateStr}`);
    }
  };
  return (
    <FullWidthMobile>
      <CalendarStyles />
      <Box flex direction="row"></Box>
      <FullCalendar
        ref={calendarRef}
        locale={ruLocale}
        plugins={[
          dayGridPlugin,
          interactionPlugin,
          timeGridPlugin,
          googleCalendarPlugin,
        ]}
        allDaySlot={false}
        initialView="timeGridWeek"
        headerToolbar={{
          center: "dayGridMonth timeGridWeek",
        }}
        contentHeight={1500}
        googleCalendarApiKey={apiKey}
        firstDay={1}
        views={{
          dayGrid: {
            dateClick: openDay,
          },
          timeGrid: {
            slotMinTime: "14:00",
            slotMaxTime: "21:00",
            expandRows: true,
            selectable: true,
            dateClick: openSubscription,
          },
        }}
        eventClick={e => {
          e.jsEvent.preventDefault(); // don'
          console.log("event", e, e.event.id);
          navigate("/dashboard/lesson/attend/" + e.event.id);
        }}
        events={{}}
        eventSources={[
          {
            events: availiableTime,
          },
          { googleCalendarId: process.env.GATSBY_GOOGLE_CALENDAR_ID },
        ]}
        eventContent={renderEventContent}
        validRange={nowDate => {
          return {
            start: new Date(),
            end: new Date(nowDate.getFullYear(), nowDate.getMonth() + 2),
          };
        }}
        fixedWeekCount={false}
      />
    </FullWidthMobile>
  );
};

export default withFirebaseProvider(SchedulePage);
